.card img {
	height: 190px;
	width: 100%;
	object-fit: cover;
	font-weight: bold;
	font-size: 20px;
	text-transform: capitalize;
	text-align: center;
	vertical-align: middle;
}

.card-body .card-title {
	text-align: left;
	display: -webkit-box;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical; 
	height: 75px;
	overflow: hidden;
}

.card-body .card-text {
	text-align: left;
	display: -webkit-box;
	-webkit-line-clamp: 6;
	-webkit-box-orient: vertical; 
	height: 145px;
	overflow: hidden;
	margin-bottom: 8px;
}

.card-body .publishedAt {
	text-align: center;
	height: 50px;
	-webkit-line-clamp: 2;
	margin-bottom: 0px;
	position: relative;
	padding: 5px 0;
}

.publishedAt::before {
	content: "";
	position: absolute;
	height: 1px;
	width: 50%;
	background: rgba(0,0,0,0.3);
	top: 2px;
	left: 25%;
	border-radius: 50px;
}

#scrollToTop {
	opacity: 0;
	position: fixed;
	bottom: 20px;
	right: 35px;
	height: 50px;
	width: 50px;
	border-radius: 100px;
	border: none;
	outline: none;
	background: black;
	transition: all 0.5s;
	display: flex;
	align-items: center;
	justify-content: center;
	transform: rotate(-45deg);
	overflow: hidden;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.5),
	0 0 10px rgba(0, 0, 0, 0.5),
	0 0 15px rgba(0, 0, 0, 0.5);
}

#scrollToTop:hover {
	background: rgba(0, 0, 0, 0.6);
}

#scrollToTop .aro {
	height: 0;
	width: 0;
	border: 10px solid transparent;
	border-top: 10px solid white;
	border-right: 10px solid white;
	transform: translate(-3px,3px);
	transition: transform 0.4s;
}

#scrollToTop:hover .aro {
	transform: translate(2px,-2px);
}